      page2: { line: true },
      page3: { line: true },
      page4: { line: true },
      page5: { line: true },
      page8: { line: true },
      page12: { line: true },

      page6: {
        line1: { stat: true, delay: 0 },
        line2: { stat: true, delay: 800 }
      },

      page7: {
        line1: { stat: true, delay: 0 },
        line2: { stat: true, delay: 800 }
      },

      page9: {
        line11: { stat: true, delay: 0 },
        dialog1: { stat: true, delay: 2000 },
        dialog2: { stat: true, delay: 1200 }
      }, 

      page10: {},

      page11: {
        dialog: { stat: true, delay: 0 },
        elem1: { stat: true, delay: 800 },
        elem2: { stat: true, delay: 1600 },
        elem3: { stat: true, delay: 2400 },
        elem4: { stat: true, delay: 3200 }
      },

      page13: {
        tip: { stat: true, delay: 2400 },
        elem1: { stat: true, delay: 0 },
        elem2: { stat: true, delay: 800 },
        elem3: { stat: true, delay: 1600 }
      },

      page14: {},

      page15: {
        elem1: { stat: true, delay: 0 },
        elem2: { stat: true, delay: 800 },
        elem3: { stat: true, delay: 1600 }
      },

      page16: {
        elem1: { stat: true, delay: 0 },
        elem2: { stat: true, delay: 800 },
        elem3: { stat: true, delay: 1600 }
      },

      page17: {
        elem1: { stat: true, delay: 0 },
        elem2: { stat: true, delay: 800 },
        elem3: { stat: true, delay: 1600 },
        elem4: { stat: true, delay: 2400 },
        elem5: { stat: true, delay: 3000 }
      }
    }

    'p11_l8 p11_l7 p11_l6 p11_l4 p11_l5 p11_l2 p11_l1 p11_l9 p11_l3'.split(' ').forEach((name, index) => {
      pageElemStat.page10[name] = { stat: true, delay: 800 * index }
    })

    for(let i of [1,2,3,4,5,6]) {
      pageElemStat.page14[`elem${i}`] = {
        stat: true, delay: (i-1) * 800
      }
    }

    return {
      windowStyle: {
        width: `${window.innerWidth}px`,
        height: `${window.innerHeight}px`
      },
      slick_options: {
        mobileFirst: true,
        arrows: false,
        infinite: false,
        dots: false,
        swipe: false,
      },
      pageElemStat,
      c: {}
