  computed: {
    bStyle() {
      return {
        width: `${this.c.ch}px`,
        height: `${this.c.cw}px`,
        // 'margin-left': `-${this.c.cw / 2}px`,
        // 'margin-top': `-${this.c.ch / 2}px`
      }
    }
  },
  components: { Slick },
  methods: {
    afterInit(event, slick) {
      this.sPage(1)
    },
    afterChange(event, slick) {
      let current = slick.currentSlide

      for(let i of [2,3,4,5,8,12]) {
        if (current + 1 === i) {
          this.sPage(i)
        }
      }

      for(let i of [6,7,9,10,11,13,14,15,16,17]) {
        if (current + 1 === i) {
          this.mulElemPage(i)
        }
      }
    },
    sPage(num) {
      this.pageElemStat[`page${num}`].line = true
    },
    mulElemPage(num) {
      let pageElem = this.pageElemStat[`page${num}`];
      for(let elem in pageElem) {
        window.setTimeout(() => {
          pageElem[elem].stat = true
        }, pageElem[elem].delay)
      }
    },

    computeRatio() {
      const sw = 172
      const sh = 343
      let cw, ch

      // let pw = window.innerWidth
      // let ph = window.innerHeight

      let pw = 414
      let ph = 736

      // assume cw = pw
      if ((pw * sh / sw) < ph) { 
        cw = pw
        ch = pw * sh / sw
      } else {
        ch = ph
        cw = sw * ph / sh
      }

      cw = Math.round(cw)
      ch = Math.round(ch)

      console.log(`sw[${sw}] sh[${sh}] pw[${pw}] ph[${ph}] cw[${cw}] ch[${ch}]`)

      this.c = { cw, ch }
    },

    imgRatio(width, height) {

    }
  },
  mounted() {
    document.body.style.width = `${window.innerWidth}px`
    document.body.style.height = `${window.innerHeight}px`

    this.computeRatio()
  },
