<template>
  <section :style="mergeStyles(slick_container_style, bgStyle)" v-on:click="showElems">
    <!-- <img v-for="elem in page_elems[num]" src="" alt=""> -->
    <!-- <transition name="fade">
      <img 
        v-for="elem in page_elems[num]" 
        :key="JSON.stringify(elem)" 
        :src="elem[0]" 
        :style="elemStyle(num, elem)"
        v-show="elem[5]">
    </transition> -->

    <template v-for="(elem, index) in page_elems[num]">
      <transition name="fade" :key="JSON.stringify(elem)" >
        <img 
          :src="elem[0]" 
          :style="elemStyle(num, elem)"
          v-show="elem_stats[index]">
      </transition>
    </template>
  </section>
</template>

<script>
const page_elems = {
  1: [
    [625, 1192, 10, 40]
  ],

  2: [
    [264, 2109, 25, 49]
  ],

  3: [
    [599, 588, 40, 36]
  ],

  4: [
    [220, 1090, 35, 74]
  ],

  5: [
    [140, 1447, 56, 3]
  ],

  6: [
    [121, 687, 7, 3],
    [114, 847, 74, 3]
  ],

  7: [
    [60, 610, 7, 17],
    [150, 755, 72, 15]
  ],

  8: [
    [187, 1548, 7, 58]
  ],

  9: [
    [82, 501, 84, 2],
    [232, 524, 29, 82],
    [323, 526, 49, 42],
  ],
  10: [
    [242, 357, 34, 75],
    [353, 377, 50, 75],
    [325, 543, 82, 80],
    [329, 368, 72, 54],
    [291, 325, 76, 36],
    [346, 462, 51, 46],
    [217, 541, 66, 23],
    [320, 441, 2, 5],
    [429, 700, 21, 37]
  ],
  11: [
    [307, 520, 31, 30],
    [71, 229, 8, 84],
    [455, 1342, 8, 55],
    [255, 1340, 51, 66],
    [742, 1268, 51, 18]
  ],

  12: [
    [528, 1526, 5, 67]
  ],

  13: [
    [96, 1318, 8, 83],
    [431, 1345, 8, 56],
    [99, 1182, 8, 45],
    [431, 1346, 8, 18],
    [92, 1254, 51, 83],
    [305, 1345, 51, 63],
    [346, 591, 60, 30]
  ],

  14: [
    [94, 462, 8, 81],
    [1136, 1025, 8, 12],
    [187, 602, 50, 76],
    [211, 575, 50, 62],
    [196, 624, 72, 75],
    [153, 613, 72, 65],
    [212, 604, 50, 44],
    [214, 577, 50, 29],
    [192, 599, 72, 44],
    [153, 613, 72, 33],
    [36, 365, 84, 11]
  ],

  15: [
    [346, 520, 15, 19],
    [696, 1736, 0, 60],
    [793, 1739, 50, 54]
  ],

  16: [
    [328, 535, 31, 22],
    [302, 505, 50, 62],
    [352, 410, 66, 24]
  ],

  17: [
    [178, 1515, 3, 86],
    [1020, 1633, 1, 24],
    [264, 1606, 2, 5],
    [55, 1070, 61, 93],
    [1576, 1648, 50, 2]
  ]
}

for(let page in page_elems) {
  let elems = page_elems[page]

  for(let i = 0; i < elems.length; i++) {
    let img = require(`./rimg/p${page}/${i+1}.png`)
    elems[i].unshift(img)
  }
}

export default {
  props: {
    num: Number
  },
  data() {
    let bg_images = Array(18).join().split(',').reduce((m, v, i) => {
      m[`p${i+1}`] = require(`./rimg/p${i+1}.png`)
      return m
    }, {})

    let elem_stats = {}
    for(let i = 0; i < page_elems[this.num].length; i++) {
      elem_stats[i] = false 
    }

    return {
      bg_images,
      page_elems,
      elem_stats,
      c: {}
    }
  },
  computed: {
    bgStyle() {
      return {
        'background-image': `url(${this.bg_images[`p${this.num}`]})`
      }
    },
    slick_container_style() {
      return {
        width: `${this.c.cw}px`,
        height: `${this.c.ch}px`
      }
    }
  },
  methods: {
    mergeStyles(s1, s2) {
      return Object.assign({}, s1, s2)
    },
    computeRatio() {
      const sw = 1724
      const sh = 3437
      let cw, ch

      let pw = window.innerWidth
      let ph = window.innerHeight

      // assume cw = pw
      if ((pw * sh / sw) < ph) { 
        cw = pw
        ch = pw * sh / sw
      } else {
        ch = ph
        cw = sw * ph / sh
      }

      cw = Math.round(cw)
      ch = Math.round(ch)

      this.c = { cw, ch, r: sw/cw }
    },
    elemStyle(page, elem) {
      let ow = elem[1]
      let oh = elem[2]

      let t = elem[3]
      let l = elem[4]

      let tw = Math.round(ow/this.c.r)
      let th = Math.round(oh/this.c.r)

      return {
        width: `${tw}px`,
        height: `${th}px`,
        top: `${t}%`,
        left: `${l}%`,
      }
    },
    showElems() {
      // for(let i = 0; i < this.elem_stats.length; i++) {
      //   window.setTimeout(() => {
      //     this.elem_stats[i] = true
      //   }, 0)
      // }

      for(let index in this.elem_stats) {
        window.setTimeout(() => {
          this.elem_stats[index] = true
        }, 800 * index)
      }
    }
  },
  mounted() {
    this.computeRatio()

  }
}
</script>

