<template>
  <div class="container" :style="container_style">
    <slick
      class="slicking"
      :style="mergeStyles(slick_container_style, slicking_margin_style)"
      :options="slick_options">
      <!-- <slide :num="17"></slide> -->
      <slide v-for="num in genArr(17)" :key="`slide_${num}`" :num="num"></slide>
    </slick>
  </div>
</template>

<script>
import './reset.css'
import './slick/slick.css'
import './slick/slick-theme.css'
import './animate.css'
import $ from 'jquery'
import Slick from 'vue-slick'
import Slide from './Slide.vue'

export default {
  data() {
    return {
      c: {},
      slick_options: {
        mobileFirst: true,
        arrows: false,
        infinite: false,
        dots: false,
        vertical: true,
        verticalSwiping: true
      }
    }
  },
  components: {
    Slick,
    slide: Slide
  },
  computed: {
    container_style() {
      return {
        width: `${window.innerWidth}px`,
        height: `${window.innerHeight}px`
      }
    },
    slick_container_style() {
      return {
        width: `${this.c.cw}px`,
        height: `${this.c.ch}px`
      }
    },
    slicking_margin_style() {
      return {
        'margin-top': `-${this.c.ch/2}px`,
        'margin-left': `-${this.c.cw/2}px`
      }
    }
  },
  methods: {
    mergeStyles(s1, s2) {
      return Object.assign({}, s1, s2)
    },

    bgStyle(page) {
      return {
        'background-url': this.images[page].url
      }
    },

    computeRatio() {
      const sw = 172
      const sh = 343
      let cw, ch

      let pw = window.innerWidth
      let ph = window.innerHeight

      // assume cw = pw
      if ((pw * sh / sw) < ph) { 
        cw = pw
        ch = pw * sh / sw
      } else {
        ch = ph
        cw = sw * ph / sh
      }

      cw = Math.round(cw)
      ch = Math.round(ch)

      this.c = { cw, ch }
    },

    genArr(amount) {
      return Array(amount).join().split(',').map((v,i) => i+1)
    }
  },
  mounted() {
    this.computeRatio()
  }
}
</script>

<style lang="less">
.container {
  position: relative;
}

.slicking {
  position: absolute;
  top: 50%;
  left: 50%;

  section {
    width: 100%;
    height: 100%;
  }
}
</style>
